	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Canadians too patriotic for sensitive Americans","date":"09 Dec 2003","slug":"/2003-12-09-canadians-too-patriotic-for-sensitive-americans/","tags":"Y2003"},"html":"<p><a title=\"Wear the flag proudly!\" href=\"http://accordionguy.blogware.com/blog/_archives/2003/12/8/8472.html\">Canadians too patriotic for sensitive Americans</a></p>"},"next":{"fields":{"title":"Unilateral Peace","date":"08 Dec 2003","slug":"/2003-12-08-unilateral-peace/"}},"previous":{"fields":{"title":"Hamburg coalition collapses, new elections on 29 Feb","date":"09 Dec 2003","slug":"/2003-12-09-hamburg-coalition-collapses-new-elections-on-29-feb/"}}},{"node":{"fields":{"title":"Unilateral Peace","date":"08 Dec 2003","slug":"/2003-12-08-unilateral-peace/","tags":"Y2003"},"html":"<p><a title=\"Declare peace and go home?\" href=\"http://www.usefulwork.com/shark/archives/001318.html#001318\">Unilateral Peace</a></p>"},"next":{"fields":{"title":"The Ghost of Schill","date":"07 Dec 2003","slug":"/2003-12-07-the-ghost-of-schill/"}},"previous":{"fields":{"title":"Canadians too patriotic for sensitive Americans","date":"09 Dec 2003","slug":"/2003-12-09-canadians-too-patriotic-for-sensitive-americans/"}}},{"node":{"fields":{"title":"The Ghost of Schill","date":"07 Dec 2003","slug":"/2003-12-07-the-ghost-of-schill/","tags":"Y2003"},"html":"<p>The ghost of Schill continues to haunt the coalition in Hamburg. He was quiet for a while after having been <a title=\"PapaScott: No Mercy\" href=\"http://www.papascott.de/2003/08/19/2519.php\">tossed</a> <a title=\"PapaScott: Schill Out\" href=\"http://www.papascott.de/2003/08/21/2522.php\">out</a> of the Senate by Mayor von Beust. Last weekend he was re-elected as the state party chair. </p>\n<p>In the past week, however, he has returned to his old self, attacking von Beust, his successor Interior Minister Nockelmann. A new role in this coalition is unthinkable, given that von Beust has sworn to never again sit at the same table as Schill.</p>\n<p>For his party, one week of the new-old Schill was enough. The  national party board has <a title=\"Schill-Rauswurf: In Hamburg wackelt die Koalition - Politik - SPIEGEL ONLINE\" href=\"http://www.spiegel.de/politik/deutschland/0,1518,277266,00.html\">dismissed Schill as state chair</a>. It's now <a title=\"Noch'n Blogg.: Schill als Hamburger Landesvorsitzender abgesetzt\" href=\"http://lumma.de/mt/archives/000751.html#000751\">very possible</a> that Schill will form his own faction in the Hamurg Bürgerschaft, take a couple of supporters with him, and force new elections. Stay tuned.</p>"},"next":{"fields":{"title":"The American Dory","date":"07 Dec 2003","slug":"/2003-12-07-the-american-dory/"}},"previous":{"fields":{"title":"Unilateral Peace","date":"08 Dec 2003","slug":"/2003-12-08-unilateral-peace/"}}}],"pathPrefix":"","first":false,"last":false,"index":574,"pageCount":1101}}

